import Vue from 'vue'
import App from './App.vue'
import VueParticles from 'vue-particles'
import router from './router'
import  'bootstrap'
import  'jquery'
import  'popper.js'
import VueTypedJs from 'vue-typed-js'
var VueScrollTo = require('vue-scrollto');

Vue.config.productionTip = false
Vue.use(VueScrollTo, {
  container: "body",
  duration: 1000,
  easing: "ease",
  offset: 0,
  force: true,
  cancelable: true,
  onStart: false,
  onDone: false,
  onCancel: false,
  x: false,
  y: true
})
Vue.use(VueParticles)
Vue.use(VueTypedJs)
new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
